.products-offers {
  position: relative;
  text-align: center;

  @media @tablet {
    padding-top: 28px;
  }
}

.products-offers__wrapper {
  padding: 28px 20px 49px;
  margin: 0 auto;

  @media @tablet {
    max-width: 460px;
    padding-bottom: 58px;
  }

  @media @desktop {
    padding-bottom: 66px;
  }
}

.products-offers__title {
  font-size: 21px;

  @media @tablet {
    font-size: 30px;
  }
}

.products-offers__text {
  margin: 0;
  font-size: 14px;
  line-height: 24px;
  padding-top: 18px;
  padding-bottom: 35px;

  @media @tablet {
    padding-top: 24px;
    padding-bottom: 43px;
  }

  @media @desktop {
    padding-bottom: 48px;
  }
}

.products-offers__map {
  min-height: 350px;
}

.products-offers__map-poster {
  width: auto;
  display: block;
  min-height: 350px;

  &--hidden {
    display: none;
  }
}

.products-offers__placemark {
  background-color: @secondaryBackgroundColor;
  border: 5px solid #ffffff;
  border-radius: 50%;
  width: 17px;
  height: 17px;
}
